import React from 'react';

class IncidencesModal extends React.Component {
    render() {
        let modalStyle = {
            position: 'absolute',
            top: '50%',
            left: '50%',
            transform: 'translate(-50%, -50%)',
            zIndex: '9999',
            background: '#fff'
        }

        let backdropStyle = {
            position: 'absolute',
            width: '100%',
            height: '100%',
            top: '0px',
            left: '0px',
            zIndex: '9998',
            background: 'rgba(0, 0, 0, 0.3)'
        }

        let incidenceStyle = {
            margin: '10px',
        }

        const incidences = this.props.incidences.map(incidence => {
            return <div style={incidenceStyle}
                key={incidence.id}
                dangerouslySetInnerHTML={{ __html: incidence.descripcion }}/>
        })

        return (
            <div className="modal">
                <div className={this.props.className} style={modalStyle}>
                    {incidences}
                </div>
                <div className={this.props.backdropClassName} style={backdropStyle}
                    onClick={e => this.close(e)} />
            </div>
        )
    }
    close(e) {
        console.log(e);
        e.preventDefault()

        if (this.props.onClose) {
            this.props.onClose()
        }
    }
}

export default IncidencesModal


